import './styles/formradiogroup.css';
import { useState } from 'react';
import PropTypes from 'prop-types';
import RadioButton from './RadioButton';

const FormRadioGroup = ({ nameGroup, options, defaultOption, onChangeRadio }) => {
    const [selectedValue, setSelectedValue] = useState(defaultOption);

    const onRadioButton = (val) => {
        setSelectedValue(options.indexOf(val));
        onChangeRadio(options.indexOf(val));
    }

    return (
        <div className='radiogroup-container'>
            {options.map((option, key) => {
                return (
                    <RadioButton key={key} nameGroup={nameGroup} val={option} isSelected={option === options[selectedValue]} onChange={onRadioButton} />
                )
            })}
        </div>
    )
}

FormRadioGroup.propTypes = {
    nameGroup: PropTypes.string.isRequired,
    options: PropTypes.array.isRequired,
    defaultOption: PropTypes.number.isRequired
}

export default FormRadioGroup;
