const RadioSwitchButton = ({ nameGroup, val, isSelected, onChange }) => {
  return (
    <div className='tabswitcher-container'>
      <input type="radio" id={val} name={nameGroup} value={val} onChange={e => onChange(e.target.value)} checked={isSelected} />
      <label htmlFor={val}>{val}</label>
      <div className='marker'></div>
    </div>
  )
}

export default RadioSwitchButton;
