import FormButton from './FormButton';
const ResultListItem = ({ leading, priceVal, i, customClass }) => {
  // https://stackoverflow.com/a/48020189
  const clickToCopy = (e) => {
    // Revert to original value first
    let textToBeCopied = document.getElementById(e.currentTarget.previousSibling.id);
    textToBeCopied.innerText = priceVal;

    e.target.innerHTML = "copied";
    let range = document.createRange();
    range.selectNode(document.getElementById(e.currentTarget.previousSibling.id));
    window.getSelection().removeAllRanges(); // clear current selection
    window.getSelection().addRange(range); // to select text
    document.execCommand("copy");
    window.getSelection().removeAllRanges();// to deselect
    textToBeCopied.innerText = priceVal.toLocaleString('en-ID');
    
    // console.log(e.target);
    setTimeout(() => {
      e.target.innerHTML = "copy"
    }, 1000);
  }

  return (
    <div className={`result-list-inner flex ${customClass}`}>
      <div className='list-leading'>{leading}</div>
      <div className='list-content flex gap-1'>
        <div className='leading-content'>Rp</div>
        <div className='price-content flex' id={`placehlder${i}`}>{priceVal.toLocaleString('en-ID')}</div>
        <FormButton classList='button mini revealonhover' val='copy' onClick={clickToCopy} />
        {/* <input type='text' className='input-readonly' readOnly onClick={(e) => selectAndCopy(e)} value={priceVal.toLocaleString('en-ID')} /></div> */}

      </div>
    </div>
  )
}

export default ResultListItem;
