import { useState, useEffect, useRef } from 'react';
import FormOption from '../FormOption';
import FormInput from '../FormInput';
import FormButton from '../FormButton';
import ResultList from '../ResultList';
import SkeletonLoading from '../SkeletonLoading';
import { validation } from '../../constant/constant';

// Fungsi perhitungan dan daftar tarif
import calculate from '../../constant/calculate';
import { IPTransitMixPriceList } from '../../backends/PriceData';

const IPTransitMix = ({ mDiscount }) => {
  const [formData, setFormData] = useState([]);
  const [bandwidthPriceList, setBandwidthPriceList] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isErrorForm, setIsErrorForm] = useState('');

  // form validation
  const formValidation = useRef(null);
  let isFormValid;

  // hasil perhitungan
  const [result, setResult] = useState([]);

  // Ambil data untuk perhitungan
  useEffect(() => {
    const getPriceData = async () => {
      setIsLoading(true);
      setBandwidthPriceList(await IPTransitMixPriceList());
      setIsLoading(false);
    };

    getPriceData();
  }, []);


  // https://www.freecodecamp.org/news/build-forms-in-react-with-react-hook-form-library/
  const onChangeFormData = (key, val) => {
    const isExists = formData.some((data) => {
      if (Object.keys(data)[0] === key) {
        data[key] = val; // Change value on hook if exist
        return true;
      }
      return false;
    });

    !isExists && setFormData((prevProps) => ([
      ...prevProps,
      { [key]: val }
    ]));
  };

  const submitForm = (event) => {
    event.preventDefault();
    // Cek parameter wajib
    isFormValid = validation(formValidation);
    if (isFormValid[0]) {
      setIsErrorForm('');
      const calculationResult = calculate(formData, 0, bandwidthPriceList); // 0 = IP Transit Mix, 1 = IP Transit Beda bandwidth, 2 = Metro, 3 Astinet
      setResult(calculationResult);
    } else {
      setIsErrorForm([true, isFormValid[1]]);
    }
  };

  return (
    <>
      <div className='flex expanded absolute-page m-no-flex'>
        <div className='absolute-content'>
          {isLoading ? <SkeletonLoading /> : (
            <form onSubmit={submitForm} ref={formValidation} >
              <b className='calculator-title'>IP Transit Mix</b>
              <div className='form-control inner'>
                <div className='section'>
                  <label className='block'>Region</label>
                  <FormOption nameGroup='Region IP Transit Mix' options={['Pilih region', '-----', 'JaBoDeTaBek', 'Jawa', 'Sumatera', 'Kalimantan & KTI']} selectedValue={onChangeFormData} />
                  {/* <FormInput data={priceData} region={locations.filter(location => Object.keys(location).includes('Lokasi IP Transit Bandwidth'))} nameGroup='IP Transit Bandwidth' label='IP Transit Bandwidth' placeholder='Bandwidth' inputLabel='Mbps' price={calculatePrice} helperMessage='Contoh: 5' isSingleInput={true} /> */}
                </div>

                <div className='section'>
                  <label htmlFor='IP Transit Mix' className='block'>Bandwidth</label>
                  <FormInput nameGroup='IP Transit Mix' label='IP Transit Mix' clearError={isFormValid} placeholder='Bandwidth' inputLabel='Mbps' helperMessage='Contoh: 5' isSingleInput={true} submitedValue={onChangeFormData} />
                </div>

                <div className='flex gap-1 form-control no-space'>
                  <FormButton classList='button primary' typ='submit' val='Hitung' />
                  <span className={`helper ${isErrorForm ? 'error' : ''}`}>{isErrorForm ? isErrorForm : ''}</span>
                </div>
              </div>
            </form>
          )}
        </div>


        {/* Result placeholder */}
        <div className='result-list-container'>
          <ResultList data={result.length !== 0 && result} discount={mDiscount} />
        </div>
      </div>
    </>
  )
}

export default IPTransitMix
