import { Link, useHistory  } from 'react-router-dom';
import FormButton from '../FormButton';

const About = () => {
  const history = useHistory();

  const previousPage = () => {
    history.goBack();
  };

  return (
    <>
      <div className='expanded absolute-page m-no-flex'>
        <div className='absolute-content timeline' style={{ width: '100%' }}>
          <div style={{ marginBottom: '20px', background: '#ffffff', padding: '20px 20px', borderRadius: '8px' }}>⬅  Kembali ke <Link to="/kt-internal">beranda</Link> atau ke <FormButton classList='button-link text-primary inline no-padding' val='halaman sebelumnya' noSubmit={true} onClick={previousPage} />.<br /></div>
          <ul>
            <li className='timeline-item'>
              <div>
                <b>7 Juli 2021</b>
                <p>Ditambahkan fitur <a href='https://feeder.sh/' title='Ditenagai oleh feeder.sh' target='_blank' rel='noreferrer'>feedback</a>.</p>
              </div>
            </li>

            <li className='timeline-item'>
              <div>
                <b>20 Juni 2021</b>
                <p>Rilis resmi.</p>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </>
  )
}

export default About;
