import './index.css';
import { useState, useRef, useEffect } from 'react';
import { Route, Link, Switch, useLocation } from 'react-router-dom';
import './components/styles/formbutton.css';
import './components/styles/formInput.css';
import FormInput from './components/FormInput';
import countapi from 'countapi-js';
import { menuMode } from './constant/constant';

// encrypt 
import Protect from 'react-app-protect';
import 'react-app-protect/dist/index.css';

// Pages
import IPTransitMix from './components/pages/IPTransitMix';
import IPTransitBedaBW from './components/pages/IPTransitBedaBW';
import NewMetro from './components/pages/NewMetro';
import Home from './components/pages/Home';
import AstinetLite from './components/pages/AstinetLite';
import AstinetBedaBW from './components/pages/AstinetBedaBW';
import AstinetReguler from './components/pages/AstinetReguler';
import About from './components/pages/About';

// feedback hub
// https://feeder.sh
import Feedback from "feeder-react-feedback"; // import Feedback component
import "feeder-react-feedback/dist/feeder-react-feedback.css"; // import stylesheet


function App() {
  // https://medium.com/@pitipatdop/little-neat-trick-to-capture-click-outside-with-react-hook-ba77c37c7e82
  // https://codesandbox.io/s/9o3lw5792w?file=/src/Dropdown.js
  const [isMenuModeOpen, setIsMenuModeOpen] = useState(false);
  const [selectedMode, setSelectedMode] = useState('Pilih layanan');
  const [masterDiscount, setMasterDiscount] = useState(50);
  const [errorDiscount, setErrorDiscount] = useState('');
  const [isAtHome, setIsAtHome] = useState(false);
  const node = useRef();

  // Listen to route change
  const location = useLocation();

  useEffect(() => {
    let pathnameURL = location.pathname;

    if (pathnameURL.charAt(pathnameURL.length - 1) === '/') {
      pathnameURL = pathnameURL.slice(0, -1);
    }

    if (pathnameURL === '/kt-internal') {
      setIsAtHome(true);
      setSelectedMode(Object.values(menuMode[0])[0]);
    } else {
      setIsAtHome(false);
    }

    if (menuMode.filter(item => Object.keys(item)[0] === location.pathname).length > 0) {
      const selectedMenuMode = Object.values(menuMode.filter(item => Object.keys(item)[0] === pathnameURL)[0]);
      setSelectedMode(Object.values(selectedMenuMode)[0]);
    }
  }, [location.pathname])

  const onClickMode = (e) => {
    setIsMenuModeOpen(!isMenuModeOpen)
  };

  const onClickOutside = (e) => {
    if (node.current.contains(e.target)) {
      return;
    }
    setIsMenuModeOpen(false);
  }

  const onModeChange = (e) => {
    setSelectedMode(e.target.text);
    setIsMenuModeOpen(false);
  }

  const onChangeDiscount = (key, val) => {
    if (val === '') {
      setMasterDiscount(50);
      setErrorDiscount('');
    } else if (parseInt(val) > 100) {
      setErrorDiscount('Maksimal nilai 100%');
      setMasterDiscount(100);
    } else {
      setMasterDiscount(val);
      setErrorDiscount('');
    }
  }

  useEffect(() => {
    countapi.hit('aggggha.github.io', 'visits').then((result) => {
      console.log('Visited ' + result.value + ' times');
    });
  }, []);

  useEffect(() => {
    if (isMenuModeOpen) {
      document.addEventListener("mousedown", onClickOutside);
    } else {
      document.removeEventListener("mousedown", onClickOutside);
    }

    return () => {
      document.removeEventListener("mousedown", onClickOutside);
    };
  }, [isMenuModeOpen]);

  // Config password
  useEffect(() => {
    const clearSession = () => {
      localStorage.removeItem('cipher');
    }

    window.addEventListener("beforeunload", clearSession);
    window.addEventListener("onload", clearSession);
    return () => {
      window.removeEventListener("beforeunload", clearSession);
      window.removeEventListener("onload", clearSession);
    }
  }, []);


  return (
    <Protect sha512='19BE4A467EF022B2DFDF65FB708AFCC522333DAED744C71A495F8C9C870B2865B7B2BDB7BAF3C42FFFCE5E0E82F9815267B8B4C99CC7627657A68FAEC2A30A10' wrapperClass='password-prompt' title='Masukkan kode akses:'>
      <div className='App'>
        <div className='container'>
          <Feedback
            projectId="60c2e2efa8061e0004656ffb"
            primaryColor="#ff0033"
            submitButtonMsg="Send Feedback 👋" />
          <div className='container-inner relative'>
            <div className='app-header flex gap-3 m-no-flex'>
              <div className='flex gap-3 m-flex'>
                <h2>Kalkulator Tarif</h2>
                <div ref={node}>
                  {/* Mobile hamburger button version */}
                  <div className={`button-hamburger ${isMenuModeOpen ? 'focus' : ''} m-only ${isAtHome && 'homescreensplash-2'}`} onClick={onClickMode} onBlur={onClickMode}>
                    <div className='hamburger-container'>
                      <div className='hamburger first'></div>
                      <div className='hamburger second'></div>
                      <div className='hamburger third'></div>
                    </div>
                  </div>

                  {/* Larger screen button */}
                  <div className={`button-link-dropdown flex custom ${isMenuModeOpen ? 'focus' : ''} ${isAtHome && 'homescreensplash'} m-display-none`} onClick={onClickMode} onBlur={onClickMode}>
                    {selectedMode}
                    <div className='dropdown-icon'></div>
                  </div>
                  <div className={`dropdown-container m-sidebar left ${isMenuModeOpen ? 'visible animated' : 'hidden'}`}>
                    <Link to="/kt-internal/ip-transit-mix" onClick={onModeChange}>IP Transit Mix</Link>
                    <Link to="/kt-internal/ip-transit-beda-bw" onClick={onModeChange}>IP Transit Beda Bandwidth</Link>
                    <Link to="/kt-internal/new-metro" onClick={onModeChange}>New Metro</Link>
                    <Link to="/kt-internal/astinet-lite" onClick={onModeChange}>Astinet Lite</Link>
                    <Link to="/kt-internal/astinet-beda-bw" onClick={onModeChange}>Astinet Beda Bandwidth</Link>
                    <Link to="/kt-internal/astinet-reguler" onClick={onModeChange}>Astinet Reguler</Link>
                  </div>
                  <div className={`m-sidebar-overlay ${isMenuModeOpen ? 'visible animated' : 'hidden'}`} onClick={e => setIsMenuModeOpen(false)}></div>
                </div>
              </div>
              <div style={{ marginLeft: 'auto', alignItems: 'center', gap: '10px' }} className='flex m-flex-justify-end'>
                <div className={`helper ${errorDiscount !== '' && 'error'}`}>{errorDiscount || 'Diskon'}</div>
                <FormInput classList='mini align-right m-shrink' nameGroup='discount' label='discount' placeholder='Diskon: 50' inputLabel='%' isPointAndCommaAllowed={true} customError={true} submitedValue={onChangeDiscount} />
              </div>
            </div>

            <Switch>
              <Route path='/kt-internal' exact render={() => (<Home mDiscount={masterDiscount} />)} />
              <Route path='/kt-internal/ip-transit-mix' render={() => (<IPTransitMix mDiscount={masterDiscount} />)} />
              <Route path='/kt-internal/ip-transit-beda-bw' render={() => (<IPTransitBedaBW mDiscount={masterDiscount} />)} />
              <Route path='/kt-internal/new-metro' render={() => (<NewMetro mDiscount={masterDiscount} />)} />
              <Route path='/kt-internal/astinet-lite' render={() => (<AstinetLite mDiscount={masterDiscount} />)} />
              <Route path='/kt-internal/astinet-beda-bw' render={() => (<AstinetBedaBW mDiscount={masterDiscount} />)} />
              <Route path='/kt-internal/astinet-reguler' render={() => (<AstinetReguler mDiscount={masterDiscount} />)} />
              <Route path='/kt-internal/tentang' render={() => (<About />)} />
            </Switch>

            <div className='about-footer'>
              Build 20210707 – <Link to="/kt-internal/tentang">Changelog</Link>
            </div>
          </div>
        </div>
      </div>
    </Protect>
  );
}

export default App;
