import './styles/formbutton.css';

const FormButton = ({ classList, val, typ, onClick, noSubmit }) => {
  if (onClick !== undefined && !noSubmit) {
    return (<button className={classList} type={typ} onClick={onClick}>{val}</button>);
  } else {
    if (noSubmit) {
      if (onClick !== undefined) {
        return (<div className={classList} type={typ} onClick={onClick}>{val}</div>);
      } else {
        return (<div className={classList} type={typ}>{val}</div>);
      }
    } else {
      return (<button className={classList} type={typ}>{val}</button>);
    }
  }
}

export default FormButton;
