import './styles/radioswitch.css';
import { useState } from 'react';
import PropTypes from 'prop-types';
import RadioSwitchButton from './RadioSwitchButton';

const FormRadioSwitch = ({ nameGroup, options, defaultOption, onChangeRadioSwitch }) => {
    const [selectedValue, setSelectedValue] = useState(defaultOption);

    const onRadioSwitchButton = (val) => {
        setSelectedValue(options.indexOf(val));
        onChangeRadioSwitch(options.indexOf(val));
    }

    return (
        <>
            {options.map((option, key) => {
                return (
                    <RadioSwitchButton key={key} nameGroup={nameGroup} val={option} isSelected={option === options[selectedValue]} onChange={onRadioSwitchButton} />
                )
            })}
        </>
    )
}

FormRadioSwitch.propTypes = {
    nameGroup: PropTypes.string.isRequired,
    options: PropTypes.array.isRequired,
    defaultOption: PropTypes.number.isRequired
}

export default FormRadioSwitch;
