import './styles/formoption.css';

const FormOption = ({ nameGroup, options, selectedValue, innerRef }) => {
    let i = -2; // Reset key-value option
    const locationChangeHandler = (e) => {
        selectedValue(nameGroup, parseInt(e.target.value));
    }

    return (
        <>
            <div className='option-container flex'>
                <select name={nameGroup} className='inner' onChange={(e) => locationChangeHandler(e)} ref={innerRef} >
                    {options.map((option) => {
                        i++;
                        return (
                            <option key={i} value={i} disabled={i === 0 && true}>{option}</option>
                        );
                    })}
                </select>
                <div className='dropdown-icon'></div>
            </div>
        </>
    )
}

export default FormOption;
