import './styles/formInput.css'
import { useState } from 'react';
import PropTypes from 'prop-types';

const FormInput = ({ classList, nameGroup, label, desc, placeholder, inputLabel, helperMessage, isSingleInput, submitedValue, innerRef, customError, isPointAndCommaAllowed }) => {
    const [textInput, setTextInput] = useState('');
    const [errorText, setErrorText] = useState('');
    
    const clickToSelect = (e) => {
        e.target.select();
    }

    const keypressHandler = (e) => {
        if (e.keyCode === 13) {
            setErrorText(''); // Hapus semua pesan error
        }
    }

    const onChange = (e) => {
        // https://codeshare.co.uk/blog/regular-expression-regex-for-a-number-greater-than-1200/
        // https://stackoverflow.com/questions/6053541/regex-every-non-alphanumeric-character-except-white-space-or-colon
        if ((isPointAndCommaAllowed !== undefined || isPointAndCommaAllowed) && e.target.value.match(/[0-9](?!0000)\d{4,}|[2-9]\d{4,}|[^\d.]+|(?=\d{6,})\d+/gm)) {
            setErrorText('Hanya angka, tidak negatif, dan kurang dari 10000.');
        } else if (isSingleInput && e.target.value.match(/[0-9](?!0000)\d{4,}|[2-9]\d{4,}|[^\d]+|(?=\d{6,})\d+/gm)) {
            setErrorText('Hanya angka, tidak negatif, dan kurang dari 10000.');
        } else {
            setTextInput(e.target.value);
            setErrorText('');
            submitedValue(nameGroup, e.target.value);
        }
    }

    return (
        <div className={`input-container ${classList}`}>
            <span className='desc'>{desc}</span>
            <div className='inner flex'>
                {/* <input id={label} type="text" value={textInput} placeholder={placeholder} className='input-text' ref={autofocus && textInputFocus} onChange={(e) => onlyNumberAndComma(e)} onKeyDown={(e) => keypressHandler(e)} /> */}
                <input id={label} type="text" value={textInput} placeholder={placeholder} className='input-text' ref={innerRef} onChange={e => onChange(e)} onKeyDown={e => keypressHandler(e)} onFocus={e => clickToSelect(e)} />
                <span className='label'>{inputLabel}</span>
            </div>
            {(customError === undefined || customError === false) && <span className={`helper ${errorText !== '' ? 'error' : ''}`}>{errorText === '' ? helperMessage : errorText}</span>}
        </div>
    )
}

FormInput.defaultProps = {
    label: '',
    desc: '',
    placeholder: 'Type here...',
    inputLabel: '',
    helperMessage: '',
    errorMessage: '',
    autofocus: true
}

FormInput.propTypes = {
    placeholder: PropTypes.string.isRequired,
}

export default FormInput;
