import { useState, useEffect, useRef } from 'react';
import FormOption from '../FormOption';
import FormInput from '../FormInput';
import FormButton from '../FormButton';
import ResultList from '../ResultList';
import SkeletonLoading from '../SkeletonLoading';
import FormRadioSwitch from '../FormRadioSwitch';
import FormRadioGroup from '../FormRadioGroup';
import { validation } from '../../constant/constant';

// Fungsi perhitungan dan daftar tarif
import calculate from '../../constant/calculate';
import { MetroPriceList } from '../../backends/PriceData';

const NewMetro = ({ mDiscount }) => {
  const [formData, setFormData] = useState([]);
  const [bandwidthPriceList, setBandwidthPriceList] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isErrorForm, setIsErrorForm] = useState([false, '']);
  const [resultView, setResultView] = useState(0);
  const [resultMode, setResultMode] = useState(2);
  const [destCount, setDestCount] = useState([0]);

  // form validation
  const formValidation = useRef(null);
  let isFormValid;

  // hasil perhitungan
  const [result, setResult] = useState([]);

  // Ambil data untuk perhitungan
  useEffect(() => {
    const getPriceData = async () => {
      setIsLoading(true);
      const rawData = await MetroPriceList();
      setBandwidthPriceList(rawData);
      setIsLoading(false);
    };

    getPriceData();
  }, []);

  const onChangeFormData = (key, val) => {
    const isExists = formData.some((data) => {
      if (Object.keys(data)[0] === key) {
        data[key] = val; // Change value on hook if exist
        return true;
      }
      return false;
    });

    !isExists && setFormData((prevValues) => ([
      ...prevValues,
      { [key]: val }
    ]));
  };

  const submitForm = (event) => {
    event.preventDefault();
    // Cek parameter wajib
    isFormValid = validation(formValidation);
    if (isFormValid[0]) {
      setIsErrorForm([false, '']);
      const calculationResult = calculate(formData, 2, bandwidthPriceList, resultMode); // 0 = IP Transit Beda Bandwidth, 1 = IP Transit Beda bandwidth, 2 = Metro, 3 Astinet
      setResult(calculationResult);
    } else {
      setIsErrorForm([true, isFormValid[1]]);
    }
  };

  const addMoreDestination = (e) => {
    setDestCount(prevCount => [...prevCount, destCount[destCount.length - 1] + 1]);
  }

  const reduceDestination = (e, destIndex) => {
    let _formData;
    const _destCount = destCount.filter(count => count !== destIndex); // Removing the deleted form
    _formData = formData.filter(item => !Object.keys(item)[0].toLowerCase().includes(destIndex)); // removing deleted value from submitted data

    if (_destCount.length === 1) {
      _formData = _formData.filter(item => Object.keys(item)[0].toLowerCase() !== 'metro _source'); // if there are only source and destination, remove the source input bandwidth value
    }

    setDestCount(_destCount);
    setFormData(_formData);

    // Re-submit form
    const calculationResult = calculate(_formData, 2, bandwidthPriceList); // 0 = IP Transit Beda Bandwidth, 1 = IP Transit Beda bandwidth, 2 = Metro, 3 Astinet
    setResult(calculationResult, 2);
  }

  return (
    <>
      <div className='flex expanded absolute-page m-no-flex'>
        <div className='absolute-content metro'>
          {isLoading ? <SkeletonLoading /> : (
            <form onSubmit={submitForm} ref={formValidation} >
              <b className='calculator-title'>New Metro</b>
              <div className='form-control inner'>
                <div className='section'>
                  <label className='block'>Hitung sebagai:</label>
                  <FormRadioGroup nameGroup='result mode' options={['Tarif P2P', 'Tarif P2MP', 'Otomatis']} defaultOption={2} onChangeRadio={setResultMode} />
                </div>
              </div>
              <div className='form-control inner'>
                <div className='section'>
                  <b>Pusat</b>
                  <label className='block'>Region</label>
                  <FormOption nameGroup='Region Metro _source' options={['Pilih region', '-----', 'JaBoTaBek', 'Jatim', 'Jabar', 'Jateng', 'Sumbagut', 'Sumbagsel', 'Kalimantan', 'Sulawesi', 'Balnusra', 'Pamal']} selectedValue={onChangeFormData} />
                  {destCount.length > 1 && (
                    <div className='section'>
                      <label htmlFor='Metro _source' className='block'>Bandwidth</label>
                      <FormInput nameGroup='Metro _source' label='Metro _source' placeholder='Bandwidth' inputLabel='Mbps' helperMessage='Contoh: 105' isSingleInput={true} submitedValue={onChangeFormData} />
                    </div>
                  )}
                </div>
              </div>

              <div className='form-control inner' id='destContainer'>
                {destCount.map((item, index) => {
                  return (
                    <div id='destInput' key={item}>
                      <div className='section'>
                        <div className='flex expanded'>
                          <b>Cabang {index + 1}</b>
                          {index !== 0 && <FormButton classList='button-link text-primary' val='Hapus destinasi' noSubmit={true} onClick={e => reduceDestination(e, item)} />}
                        </div>
                        <label className='block'>Region</label>
                        <FormOption nameGroup={`Region Metro _destination ${item}`} options={['Pilih region', '-----', 'JaBoTaBek', 'Jatim', 'Jabar', 'Jateng', 'Sumbagut', 'Sumbagsel', 'Kalimantan', 'Sulawesi', 'Balnusra', 'Pamal']} selectedValue={onChangeFormData} />
                      </div>

                      <div className='section'>
                        <label htmlFor={`Metro _destination ${item}`} className='block'>Bandwidth</label>
                        <FormInput nameGroup={`Metro _destination ${item}`} label={`Metro _destination ${item}`} placeholder='Bandwidth' inputLabel='Mbps' helperMessage='Contoh: 15' isSingleInput={true} submitedValue={onChangeFormData} />
                      </div>
                    </div>
                  );
                })}
              </div>
              <div className='flex gap-1 form-control no-space'>
                <FormButton classList='button primary' typ='submit' val='Hitung' />
                <FormButton classList='button' val='Tambah input baru' noSubmit={true} onClick={addMoreDestination} />
                <span className={`helper ${isErrorForm[0] ? 'error' : ''}`}>{isErrorForm[0] ? isErrorForm[1] : ''}</span>
              </div>
            </form>
          )}
        </div>


        {/* Result placeholder */}
        <div className='result-list-container'>
          {result.length !== 0 && (
            <div className='switch-view flex m-block m-text-center'>
              <span>Tampilkan tarif: </span>
              <div className='flex option m-inline-flex'>
                <FormRadioSwitch nameGroup='result view' options={['Best Effort', 'Interaktif']} defaultOption={0} onChangeRadioSwitch={setResultView} />
              </div>
            </div>
          )}
          <div>
            <ResultList data={result.length !== 0 && result} view={resultView && resultView} discount={mDiscount} />
          </div>
        </div>
      </div>
    </>
  )
}

export default NewMetro;
