// https://gsx2json.com/
const IPTransitMixEndpoint = 'https://gsx2json.com/api?id=1thnl33endXVxyNkZ_XBICftyUHV9PBcedoIx2jWyJpk&sheet=1&columns=false';
const IPTransitBedaBWEndpoint = 'https://gsx2json.com/api?id=1thnl33endXVxyNkZ_XBICftyUHV9PBcedoIx2jWyJpk&sheet=2&columns=false';
const MetroEndpoint = 'https://gsx2json.com/api?id=1thnl33endXVxyNkZ_XBICftyUHV9PBcedoIx2jWyJpk';
const AstinetLiteEndpoint = 'https://gsx2json.com/api?id=1thnl33endXVxyNkZ_XBICftyUHV9PBcedoIx2jWyJpk&sheet=13&columns=false';
const AstinetBedaBWEndpoint = 'https://gsx2json.com/api?id=1thnl33endXVxyNkZ_XBICftyUHV9PBcedoIx2jWyJpk&sheet=14&columns=false';
const AstinetRegulerEndpoint = 'https://gsx2json.com/api?id=1thnl33endXVxyNkZ_XBICftyUHV9PBcedoIx2jWyJpk&sheet=15&columns=false';

const regionIPTransit = ['JaBoDeTaBek', 'Jawa', 'Sumatera', 'Kalimantankti'];
const regionAstinetRegBedaBW = ['JaBoDeTaBek', 'Jawa', 'Sumatera', 'Kalimantankti', 'Satelit'];
const regionMetro = ['JaBoTaBek', 'Jatim', 'Jabar', 'Jateng', 'Sumbagut', 'Sumbagsel', 'Kalimantan', 'Sulawesi', 'Balnusra', 'Pamal'];

const calculatorMode = [
    'IP Transit Mix',
    'IP Transit Beda Bandwidth',
    'Metro Ethernet'
];

const menuMode = [
    { '/kt-internal': 'Pilih Layanan' },
    { '/kt-internal/ip-transit-mix': 'IP Transit Mix' },
    { '/kt-internal/ip-transit-beda-bw': 'IP Transit Beda Bandwidth' },
    { '/kt-internal/new-metro': 'New Metro' },
    { '/kt-internal/astinet-lite': 'Astinet Lite' },
    { '/kt-internal/astinet-beda-bw': 'Astinet Beda Bandwidth' },
    { '/kt-internal/astinet-reguler': 'Astinet Reguler' }
];

const validation = (e, mode) => {
    let isNotError = false;
    for (let i = 0; i < e.current.elements.length - 1; i++) {
        if (e.current[i].type === 'select-one') {
            if (parseInt(e.current[i].value) > 0) {
                isNotError = [true];
            } else {
                isNotError = [false, 'Region belum dipilih.'];
                break;
                //   isNotError = false;
            }
        } else if (e.current[i].type === 'text') {
            if (e.current[i].value === '') {
                isNotError = [false, 'Bandwidth belum terisi.'];
                break;
                //   isNotError = false;
            } else if (mode === 3 && parseInt(e.current[i].value) > 100) {
                isNotError = [false, 'Bandwidth maksimum = 100.'];
                break;
            } else if ((mode === 4 || mode === 5) && parseInt(e.current[i].value) > 1000) {
                isNotError = [false, 'Bandwidth maksimum = 1000.'];
                break;
            }
        } else {
            isNotError = [true];
        }
    }

    return isNotError;
}

export { IPTransitMixEndpoint, IPTransitBedaBWEndpoint, MetroEndpoint, AstinetLiteEndpoint, AstinetBedaBWEndpoint, AstinetRegulerEndpoint, regionIPTransit, regionAstinetRegBedaBW, regionMetro, calculatorMode, validation, menuMode };