import { Link } from 'react-router-dom';

const Home = () => {
  return (
    <>
      <div className='flex expanded absolute-page m-no-flex'>
        <div className='absolute-content'>
          <div className='homescreen-message align-right m-align-unset'>
            <div className='homescreen-arrow'>⬆</div>
            <div className='homescreen-content'>Pilih layanan yang akan dihitung melalui menu di atas. Atau, pilih link disamping.</div>
          </div>
        </div>

        <div className='result-list-container'>
          <h3>Jalan pintas:</h3>
          <ol style={{ padding: '0 17px', lineHeight: '1.5' }}>
            <li><Link to="/kt-internal/ip-transit-mix">Klik disini</Link> untuk menghitung tarif <b>IP Transit Mix</b></li>
            <li><Link to="/kt-internal/ip-transit-beda-bw">Klik disini</Link> untuk menghitung tarif <b>IP Transit Beda Bandwidth</b></li>
            <li><Link to="/kt-internal/new-metro">Klik disini</Link> untuk menghitung tarif <b>New Metro Ethernet</b></li>
            <li><Link to="/kt-internal/astinet-lite">Klik disini</Link> untuk menghitung tarif <b>Astinet Lite</b></li>
            <li><Link to="/kt-internal/astinet-beda-bw">Klik disini</Link> untuk menghitung tarif <b>Astinet Beda Bandwidth</b></li>
            <li><Link to="/kt-internal/astinet-reguler">Klik disini</Link> untuk menghitung tarif <b>Astinet Reguler</b></li>
          </ol>
        </div>
      </div>
    </>
  )
}

export default Home;
