import './styles/resultlist.css';
import ResultListItem from './ResultListItem';

const ResultList = ({ data, view, discount }) => {
  let sumPrice = 0;
  let sumBandwidth = 0

  // rendering option
  let viewToBeRender = [];
  let viewToBeRenderDiscount = [];
  let firstDataIndex;
  // let firstView;

  if (data) {
    switch (data[0].mode) {
      // case 0: // IP Transit Mix
      //   for (let i = 0; i < data.length; i++) {
      //     sumBandwidth = sumBandwidth + data[i].bandwidth;
      //     sumPrice = sumPrice + data[i].tarif;
      //   }
      //   break;
      // case 1: // IP Transit Beda Bandwidth
      //   for (let i = 0; i < data.length; i++) {
      //     sumBandwidth = sumBandwidth + data[i].bandwidth;
      //     sumPrice = sumPrice + data[i].tarif;
      //   }
      //   break;
      case 2: // New Metro
        for (let i = 0; i < data.length; i++) {
          sumBandwidth = sumBandwidth + data[i].bandwidth;
          if (view < 2) {
            sumPrice = sumPrice + Object.values(data[i].tarif[view])[0];
          } else {
            for (let j = 0; j < data[i].tarif.length; j++) {
              sumPrice = sumPrice + Object.values(data[i].tarif[j])[0];
            }
          }
        }
        break;
      default:
        for (let i = 0; i < data.length; i++) {
          sumBandwidth = sumBandwidth + data[i].bandwidth;
          sumPrice = sumPrice + data[i].tarif;
        }
        break;
    }


  } else {
    sumPrice = false;
  }

  if (!data) {
    return (
      <div>
        <b>Disclaimer:</b>
        <ol style={{ padding: '0 25px' }}>
          <li>Kalkulator ini merupakan alat bantu perhitungan saja.</li>
          <li>Jika terdapat perbedaan dan kesalahan perhitungan tetap mengacu pada NDE.</li>
        </ol>
      </div>
    )
  } else {
    viewToBeRender = []; // Flush array
    viewToBeRenderDiscount = []; // Flush array
    // Generating unique key value
    let uniqueKey = [];

    // for ease of sorting, grab the source/global value and region first
    let i = 1;
    let j = 0;
    if (data.some(item => item.isGlobal || item.isSource) && (data[0].submode !== undefined || data[0].submode !== 0)) {
      firstDataIndex = data.map(item => item.isGlobal || item.isSource).indexOf(true);
    } else {
      if (data[0].submode === undefined || data[0].submode === 0) {
        firstDataIndex = null;
        viewToBeRender[0] = null;
      }
    }

    // Tarif sebelum diskon
    if (data[0].submode !== undefined && data[0].submode === 0 && data[0].mode === 2) {
      // Reset everything
      viewToBeRender = [];

      for (let x = 2; x < data.length; x++) {
        // Generating key
        let generatedKey = Math.floor(Math.random() * 100) + 1;
        if (uniqueKey.indexOf(generatedKey) !== -1) uniqueKey.push(generatedKey);

        if (Object.keys(data[x].tarif[view])[0].toLowerCase().includes('_inter')) {
          sumPrice = 0;
          viewToBeRender[j] = <b className={`submode-metro-list`}>P2P {data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region} - {data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}</b>
          j++;
          viewToBeRender[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[1].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[1].bandwidth}Mbps ${data[1].isGlobal !== undefined ? data[1].isGlobal ? 'global' : 'domestik' : ''} ${data[1].isSource !== undefined ? data[1].isSource ? 'dari' : 'ke' : ''} ${data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region}`} priceVal={Object.values(data[1].tarif[view])} />;
          sumPrice += Object.values(data[1].tarif[view])[0];
          j++;
          viewToBeRender[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[x].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[x].bandwidth}Mbps ${data[x].isGlobal !== undefined ? data[x].isGlobal ? 'global' : 'domestik' : ''} ${data[x].isSource !== undefined ? data[x].isSource ? 'dari' : 'ke' : ''} ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={Object.values(data[x].tarif[view])} />;
          sumPrice += Object.values(data[x].tarif[view])[0];
          j++;
          // Result holder
          viewToBeRender[j] = <ResultListItem i={`zz${x}`} key={`zz${x}`} customClass={`submode-metro`} leading={`Total P2P ${data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region} - ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={sumPrice} />
        } else {
          sumPrice = 0;
          viewToBeRender[j] = <b className={`submode-metro-list`}>P2P {data[0].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[0].region} - {data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}</b>
          j++;
          viewToBeRender[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[0].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[0].bandwidth}Mbps ${data[0].isGlobal !== undefined ? data[0].isGlobal ? 'global' : 'domestik' : ''} ${data[0].isSource !== undefined ? data[0].isSource ? 'dari' : 'ke' : ''} ${data[0].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[0].region}`} priceVal={Object.values(data[0].tarif[view])} />;
          sumPrice += Object.values(data[0].tarif[view])[0];
          j++;
          sumPrice += Object.values(data[x].tarif[view])[0];
          viewToBeRender[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[x].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[x].bandwidth}Mbps ${data[x].isGlobal !== undefined ? data[x].isGlobal ? 'global' : 'domestik' : ''} ${data[x].isSource !== undefined ? data[x].isSource ? 'dari' : 'ke' : ''} ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={Object.values(data[x].tarif[view])} />;
          j++;
          // Result holder
          viewToBeRender[j] = <ResultListItem i={`zz${x}`} key={`zz${x}`} customClass={`submode-metro`} leading={`Total P2P ${data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region} - ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={sumPrice} />
        }
        j++;
      }

    } else {
      // Reset everything
      viewToBeRender = [];

      data.map((item, index) => {
        if (index !== firstDataIndex) {
          viewToBeRender[i] = <ResultListItem i={index} key={index} leading={`Tarif ${item.mode === 2 ? Object.keys(item.tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra' : ''} ${item.bandwidth}Mbps ${item.isGlobal !== undefined ? item.isGlobal ? 'global' : 'domestik' : ''} ${item.isSource !== undefined ? item.isSource ? 'dari' : 'ke' : ''} ${item.region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : item.region}`} priceVal={item.mode === 2 ? Object.values(item.tarif[view]) : item.tarif} />;
          i++
        } else {
          viewToBeRender[0] = <ResultListItem i={index} key={index} leading={`Tarif ${item.mode === 2 ? Object.keys(item.tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra' : ''} ${data[firstDataIndex].bandwidth}Mbps ${data[firstDataIndex].isGlobal ? 'global' : ''} ${data[firstDataIndex].isSource ? 'dari' : ''} ${data[firstDataIndex].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[firstDataIndex].region}`} priceVal={data[firstDataIndex].mode === 2 ? Object.values(data[firstDataIndex].tarif[view]) : data[firstDataIndex].tarif} />;
        }
        return null;
      });
    }



    // Tarif setelah diskon
    if (data[0].submode !== undefined && data[0].submode === 0 && data[0].mode === 2) {
      // Reset everything
      viewToBeRenderDiscount = [];

      for (let x = 2; x < data.length; x++) {
        // Generating key
        let generatedKey = Math.floor(Math.random() * 100) + 1;
        if (uniqueKey.indexOf(generatedKey) !== -1) uniqueKey.push(generatedKey);

        if (Object.keys(data[x].tarif[view])[0].toLowerCase().includes('_inter')) {
          sumPrice = 0;
          viewToBeRenderDiscount[j] = <b className='submode-metro-list'>P2P {data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region} - {data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}</b>
          j++;
          viewToBeRenderDiscount[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[1].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[1].bandwidth}Mbps ${data[1].isGlobal !== undefined ? data[1].isGlobal ? 'global' : 'domestik' : ''} ${data[1].isSource !== undefined ? data[1].isSource ? 'dari' : 'ke' : ''} ${data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region}`} priceVal={Object.values(data[1].tarif[view]) * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />;
          sumPrice += Object.values(data[1].tarif[view])[0];
          j++;
          viewToBeRenderDiscount[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[x].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[x].bandwidth}Mbps ${data[x].isGlobal !== undefined ? data[x].isGlobal ? 'global' : 'domestik' : ''} ${data[x].isSource !== undefined ? data[x].isSource ? 'dari' : 'ke' : ''} ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={Object.values(data[x].tarif[view]) * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />;
          sumPrice += Object.values(data[x].tarif[view])[0];
          j++;
          // Result holder
          viewToBeRenderDiscount[j] = <ResultListItem i={`zz${x}`} customClass={`submode-metro`} leading={`Total P2P ${data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region} - ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={sumPrice * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />
        } else {
          sumPrice = 0;
          viewToBeRenderDiscount[j] = <b className={`submode-metro-list`}>P2P {data[0].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[0].region} - {data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}</b>
          j++;
          viewToBeRenderDiscount[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[0].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[0].bandwidth}Mbps ${data[0].isGlobal !== undefined ? data[0].isGlobal ? 'global' : 'domestik' : ''} ${data[0].isSource !== undefined ? data[0].isSource ? 'dari' : 'ke' : ''} ${data[0].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[0].region}`} priceVal={Object.values(data[0].tarif[view]) * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />;
          sumPrice += Object.values(data[0].tarif[view])[0];
          j++;
          viewToBeRenderDiscount[j] = <ResultListItem customClass={`submode-metro-list`} i={uniqueKey[j]} key={uniqueKey[j]} leading={`Tarif ${Object.keys(data[x].tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra'} ${data[x].bandwidth}Mbps ${data[x].isGlobal !== undefined ? data[x].isGlobal ? 'global' : 'domestik' : ''} ${data[x].isSource !== undefined ? data[x].isSource ? 'dari' : 'ke' : ''} ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={Object.values(data[x].tarif[view]) * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />;
          sumPrice += Object.values(data[x].tarif[view])[0];
          j++;
          // Result holder
          viewToBeRenderDiscount[j] = <ResultListItem i={`zz${x}`} customClass={`submode-metro`} leading={`Total P2P ${data[1].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[1].region} - ${data[x].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[x].region}`} priceVal={sumPrice * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />
        }
        j++;
      }

    } else {
      // Reset everything
      viewToBeRenderDiscount = [];

      data.map((item, index) => {
        if (index !== firstDataIndex) {
          viewToBeRenderDiscount[i] = <ResultListItem i={index + 2242} key={index} leading={`Tarif ${item.mode === 2 ? Object.keys(item.tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra' : ''} ${item.bandwidth}Mbps ${item.isGlobal !== undefined ? item.isGlobal ? 'global' : 'domestik' : ''} ${item.isSource !== undefined ? item.isSource ? 'dari' : 'ke' : ''} ${item.region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : item.region}`} priceVal={(item.mode === 2 ? Object.values(item.tarif[view]) : item.tarif) * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />;
          i++
        } else {
          viewToBeRenderDiscount[0] = <ResultListItem i={index + 2242} key={index} leading={`Tarif ${item.mode === 2 ? Object.keys(item.tarif[view])[0].toLowerCase().includes('_inter') ? 'inter' : 'intra' : ''} ${data[firstDataIndex].bandwidth}Mbps ${data[firstDataIndex].isGlobal ? 'global' : ''} ${data[firstDataIndex].isSource ? 'dari' : ''} ${data[firstDataIndex].region.toLowerCase().includes('kalimantankti') ? 'Kalimantan & KTI' : data[firstDataIndex].region}`} priceVal={(data[firstDataIndex].mode === 2 ? Object.values(data[firstDataIndex].tarif[view]) : data[firstDataIndex].tarif) * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />;
        }
        return null;
      });
    }


    return (
      <>
        <ResultListItem i='zzzzz' leading='Biaya aktivasi integrasi' priceVal={2500000} />
        <br />

        {/* Sebelum diskon */}
        <div className='before-discount' >
          <b style={{ display: 'block' }}>Tarif</b>
          {viewToBeRender}

          {/* Grand total */}
          {data[0].submode !== 0 && <ResultListItem i='z' leading={`Total ${sumBandwidth}Mbps`} priceVal={sumPrice} />}
        </div>

        <div className='after-discount' >
          {/* Setelah diskon */}
          <b style={{ display: 'block' }}>Tarif setelah diskon {(discount !== undefined || discount !== '') ? discount : 50}%</b>
          {viewToBeRenderDiscount}

          {/* Grand total */}
          {data[0].submode !== 0 && <ResultListItem i='zz' leading={`Total ${sumBandwidth}Mbps`} priceVal={sumPrice * (1 - ((discount !== undefined || discount !== '' ? discount : 50) / 100))} />}
        </div>
      </>
    )
  }
}

export default ResultList;
