import { calculatorMode, regionIPTransit, regionMetro, regionAstinetRegBedaBW } from './constant';

const calculate = (formData, mode, data, subModeMetro) => {
  // Parse data terlebih dahulu
  let _regionList;
  let _priceToCalcList;
  let calculatedPrice = [];
  let locationCheck = 0;
  let isInter = false;
  let _globalOrSourcePrice; // for fool proof location mistakes
  let _globalOrSourceRegion; // for fool proof location mistakes
  let j = 1;


  // Gathering region only data
  _regionList = formData.filter(item => {
    if (Object.keys(item)[0].toLowerCase().includes('region')) {
      return item;
    } else {
      return null;
    }
  });

  // Gathering price only data
  _priceToCalcList = formData.filter(item => {
    if (!Object.keys(item)[0].toLowerCase().includes('region')) {
      return item;
    } else {
      return null;
    }
  });

  // grabbing source/global value and region for ease of sorting
  _globalOrSourceRegion = _regionList.filter(item => Object.keys(item)[0].toLowerCase().includes('source') || (mode !== 2 && Object.keys(item)[0].toLowerCase().includes('region')));
  _globalOrSourcePrice = _priceToCalcList.filter(item => Object.keys(item)[0].toLowerCase().includes('source') || Object.keys(item)[0].toLowerCase().includes('global'));

  // Eliminate non-source/non-global value and region
  _regionList = _regionList.filter(item => {
    if (Object.keys(item)[0].toLowerCase().includes('destination') || (mode !== 2 && Object.keys(item)[0].toLowerCase().includes('region'))) {
      return item;
    } else {
      return null;
    }
  });

  _priceToCalcList = _priceToCalcList.filter(item => {
    if (Object.keys(item)[0].toLowerCase().includes('destination') || (!Object.keys(item)[0].toLowerCase().includes('global') && !Object.keys(item)[0].toLowerCase().includes('source'))) {
      return item;
    } else {
      return null;
    }
  });


  // POV REGION
  if (subModeMetro !== undefined && subModeMetro === 0) {
    calculatedPrice = [];
    j = 2;
    for (let i = 0; i < _regionList.length; i++) {
      // Check for intra - inter
      if (mode === 2) {
        locationCheck = Object.values(_globalOrSourceRegion[0]);

        if (Object.values(_regionList[i === (_regionList.length - 1) ? (_regionList.length - 1) : i])[0] === locationCheck[0]) {
          isInter = false;
        } else {
          isInter = true;
        }
      }


      if (i === 0) {
        calculatedPrice[0] = _calculate(
          _globalOrSourcePrice[0] !== undefined ? Object.values(_globalOrSourcePrice[0])[0] : Object.values(_priceToCalcList[i === (_regionList.length - 1) ? (_priceToCalcList.length - 1) : i])[0],
          _globalOrSourceRegion[0] !== undefined && Object.values(_globalOrSourceRegion[0])[0],
          data,
          mode,
          true,
          false,
          subModeMetro
        ); // hitung dan tambahkan nilai ke array
        calculatedPrice[1] = _calculate(
          _globalOrSourcePrice[0] !== undefined ? Object.values(_globalOrSourcePrice[0])[0] : Object.values(_priceToCalcList[i === (_regionList.length - 1) ? (_priceToCalcList.length - 1) : i])[0],
          _globalOrSourceRegion[0] !== undefined && Object.values(_globalOrSourceRegion[0])[0],
          data,
          mode,
          true,
          true,
          subModeMetro
        ); // hitung dan tambahkan nilai ke array
      }

      if ((_priceToCalcList.length + _globalOrSourcePrice.length) > 1 || mode === 2) {
        calculatedPrice[j] = _calculate(
          Object.values(_priceToCalcList[i === (_regionList.length - 1) ? (_priceToCalcList.length - 1) : i])[0],
          Object.values(_regionList[i === (_regionList.length - 1) ? (_regionList.length - 1) : i])[0],
          data,
          mode,
          false,
          isInter,
          subModeMetro
        ); // hitung dan tambahkan nilai ke array
        j++;
      }
    }
    return calculatedPrice;
  } else {
    calculatedPrice = [];
    for (let i = 0; i < _regionList.length; i++) {
      // Check for intra - inter
      if (mode === 2) {
        locationCheck = Object.values(_globalOrSourceRegion[0]);

        if (Object.values(_regionList[i === (_regionList.length - 1) ? (_regionList.length - 1) : i])[0] === locationCheck[0]) {
          isInter = false;
        } else {
          isInter = true;
        }
      }


      if (i === 0) {
        calculatedPrice[0] = _calculate(
          _globalOrSourcePrice[0] !== undefined ? Object.values(_globalOrSourcePrice[0])[0] : Object.values(_priceToCalcList[i === (_regionList.length - 1) ? (_priceToCalcList.length - 1) : i])[0],
          _globalOrSourceRegion[0] !== undefined && Object.values(_globalOrSourceRegion[0])[0],
          data,
          mode,
          true,
          _priceToCalcList.length > 1 ? true : isInter
        ); // hitung dan tambahkan nilai ke array
      }

      if ((_priceToCalcList.length + _globalOrSourcePrice.length) > 1 || mode === 2) {
        calculatedPrice[j] = _calculate(
          Object.values(_priceToCalcList[i === (_regionList.length - 1) ? (_priceToCalcList.length - 1) : i])[0],
          Object.values(_regionList[i === (_regionList.length - 1) ? (_regionList.length - 1) : i])[0],
          data,
          mode,
          false,
          isInter
        ); // hitung dan tambahkan nilai ke array
        j++;
      }
    }
    return calculatedPrice;
  }
}

const _calculate = (value, locationToCalc, data, mode, isGlobalOrSource, isInter, subModeMetro, isMP2MP) => {
  let roundedBW = Math.floor(value / 10) * 10;

  // Holds result and prepopulated result
  let region;
  let result;
  let preResult;
  let priceX;
  let isInterpolationNeeded = false;
  let maxVal;
  let minVal;
  let filteredKey;
  let _result = [];

  // for metro or astinet
  let _maxVal = [];
  let _minVal = [];

  if (mode === 0 || mode === 1) {
    region = regionIPTransit[locationToCalc - 1]
    if ((value > 10 && value < 20) || (value > 20 && value < 50) || (value > 50 && value < 100) || (value > 100 && value < 200) || (value > 200 && value < 500) || (value > 500 && value < 600) || (value > 600 && value < 700) || (value > 700 && value < 800) || (value > 800 && value < 900) || (value > 900 && value < 1000) || (value > 1000 && value < 2000) || (value > 2000 && value < 3000) || (value > 3000 && value < 4000) || (value > 4000 && value < 5000) || (value > 5000 && value < 6000) || (value > 6000 && value < 7000) || (value > 7000 && value < 8000) || (value > 8000 && value < 9000) || (value > 9000 && value < 10000)) {
      isInterpolationNeeded = true;
      if (roundedBW > 20 && roundedBW < 50) {
        roundedBW = 20;
      } else if (roundedBW > 50 && roundedBW < 100) {
        roundedBW = 50;
      } else if (roundedBW > 100 && roundedBW < 200) {
        roundedBW = 100;
      } else if (roundedBW > 200 && roundedBW < 500) {
        roundedBW = 200;
      } else if (roundedBW > 500 && roundedBW < 600) {
        roundedBW = 500;
      } else if (roundedBW > 600 && roundedBW < 700) {
        roundedBW = 600;
      } else if (roundedBW > 700 && roundedBW < 800) {
        roundedBW = 700;
      } else if (roundedBW > 800 && roundedBW < 900) {
        roundedBW = 800;
      } else if (roundedBW > 900 && roundedBW < 1000) {
        roundedBW = 900;
      } else if (roundedBW > 1000 && roundedBW < 2000) {
        roundedBW = 1000;
      } else if (roundedBW > 2000 && roundedBW < 3000) {
        roundedBW = 2000;
      } else if (roundedBW > 3000 && roundedBW < 4000) {
        roundedBW = 3000;
      } else if (roundedBW > 4000 && roundedBW < 5000) {
        roundedBW = 4000;
      } else if (roundedBW > 5000 && roundedBW < 6000) {
        roundedBW = 5000;
      } else if (roundedBW > 6000 && roundedBW < 7000) {
        roundedBW = 6000;
      } else if (roundedBW > 7000 && roundedBW < 8000) {
        roundedBW = 7000;
      } else if (roundedBW > 8000 && roundedBW < 9000) {
        roundedBW = 8000;
      } else if (roundedBW > 9000 && roundedBW < 10000) {
        roundedBW = 9000;
      }
    }
  } else if (mode === 2) {
    region = regionMetro[locationToCalc - 1]
    if ((value > 10 && value < 100) || (value > 100 && value < 200) || (value > 200 && value < 300) || (value > 300 && value < 400) || (value > 400 && value < 500) || (value > 500 && value < 600) || (value > 600 && value < 700) || (value > 700 && value < 800) || (value > 800 && value < 900) || (value > 900 && value < 1000) || (value > 1000 && value < 2000) || (value > 2000 && value < 3000) || (value > 3000 && value < 4000) || (value > 4000 && value < 5000) || (value > 5000 && value < 6000) || (value > 6000 && value < 7000) || (value > 7000 && value < 8000) || (value > 8000 && value < 9000) || (value > 9000 && value < 10000)) {
      isInterpolationNeeded = true;
      if (roundedBW > 100 && roundedBW < 200) {
        roundedBW = 100;
      } else if (roundedBW > 200 && roundedBW < 300) {
        roundedBW = 200;
      } else if (roundedBW > 300 && roundedBW < 400) {
        roundedBW = 300;
      } else if (roundedBW > 400 && roundedBW < 500) {
        roundedBW = 400;
      } else if (roundedBW > 500 && roundedBW < 600) {
        roundedBW = 500;
      } else if (roundedBW > 600 && roundedBW < 700) {
        roundedBW = 600;
      } else if (roundedBW > 700 && roundedBW < 800) {
        roundedBW = 700;
      } else if (roundedBW > 800 && roundedBW < 900) {
        roundedBW = 800;
      } else if (roundedBW > 900 && roundedBW < 1000) {
        roundedBW = 900;
      } else if (roundedBW > 1000 && roundedBW < 2000) {
        roundedBW = 1000;
      } else if (roundedBW > 2000 && roundedBW < 3000) {
        roundedBW = 2000;
      } else if (roundedBW > 3000 && roundedBW < 4000) {
        roundedBW = 3000;
      } else if (roundedBW > 4000 && roundedBW < 5000) {
        roundedBW = 4000;
      } else if (roundedBW > 5000 && roundedBW < 6000) {
        roundedBW = 5000;
      } else if (roundedBW > 6000 && roundedBW < 7000) {
        roundedBW = 6000;
      } else if (roundedBW > 7000 && roundedBW < 8000) {
        roundedBW = 7000;
      } else if (roundedBW > 8000 && roundedBW < 9000) {
        roundedBW = 8000;
      } else if (roundedBW > 9000 && roundedBW < 10000) {
        roundedBW = 9000;
      }
    }
  } else if (mode === 3) {
    region = regionIPTransit[locationToCalc - 1]
    if (value < 10) {
      roundedBW = Math.floor(value / 1) * 1;
    } else {
      roundedBW = Math.floor(value / 10) * 10;
    }

    if ((value > 5 && value < 10) || (value > 10 && value < 100)) {
      isInterpolationNeeded = true;
      if (roundedBW > 5 && roundedBW < 10) {
        roundedBW = 5;
      }
    }
  } else if (mode === 4 || mode === 5) {
    region = regionAstinetRegBedaBW[locationToCalc - 1]
    if ((value > 10 && value < 20) || (value > 20 && value < 50) || (value > 50 && value < 100) || (value > 100 && value < 200) || (value > 200 && value < 500) || (value > 500 && value < 1000)) {
      isInterpolationNeeded = true;
      if (roundedBW > 20 && roundedBW < 50) {
        roundedBW = 20;
      } else if (roundedBW > 50 && roundedBW < 100) {
        roundedBW = 50;
      } else if (roundedBW > 100 && roundedBW < 200) {
        roundedBW = 100;
      } else if (roundedBW > 200 && roundedBW < 500) {
        roundedBW = 200;
      } else if (roundedBW > 500 && roundedBW < 1000) {
        roundedBW = 500;
      }
    }
  }

  if (isInterpolationNeeded) {
    // Interpolasi Linear
    // Y = Y1 + (X - X1)/(X2 - X1) * (Y2 - Y1)
    // X = Bandwidth yang diinginkan
    // X1 = Bandwidth Min
    // X2 = Bandwidth Max
    // Y1 = Tarif Bandwidth Min
    // Y2 = Tarif Bandwidth Max
    if (mode === 0 || mode === 1 || mode === 3 || mode === 4 || mode === 5) {
      minVal = data.rows[data.rows.map((list) => list.bandwidthmbps).indexOf(roundedBW)];
      maxVal = data.rows[data.rows.map((list) => list.bandwidthmbps).indexOf(roundedBW) + 1];
    } else if (mode === 2) {
      preResult = data.filter(item => Object.keys(item)[0] === region);
      minVal = [preResult[0][region][preResult[0][region].map((list) => list.bandwidthmbps).indexOf(roundedBW)]];
      filteredKey = Object.keys(minVal[0]).filter(item => (item.toLowerCase().includes(isInter ? '_inter' : '_intra') && item.includes(isMP2MP ? 'MP2MP' : 'P2P')));
      minVal.filter(item =>
        filteredKey.map((key, index) => {
          _minVal[index] = { [key]: item[key], 'bandwidthmbps': item.bandwidthmbps };
          return false;
        })
      );

      maxVal = [preResult[0][region][preResult[0][region].map((list) => list.bandwidthmbps).indexOf(roundedBW) + 1]];
      filteredKey = Object.keys(maxVal[0]).filter(item => (item.toLowerCase().includes(isInter ? '_inter' : '_intra') && item.includes(isMP2MP ? 'MP2MP' : 'P2P')));
      maxVal.filter(item =>
        filteredKey.map((key, index) => {
          _maxVal[index] = { [key]: item[key], 'bandwidthmbps': item.bandwidthmbps };
          return false;
        })
      );

      _result = [_minVal, _maxVal];
    }

    switch (mode) {
      case 0:
      case 3:
      case 5:
        priceX = minVal[region.toLowerCase()] + (((value - minVal.bandwidthmbps) / (maxVal.bandwidthmbps - minVal.bandwidthmbps)) * (maxVal[region.toLowerCase()] - minVal[region.toLowerCase()]));
        return {
          "mode": mode,
          "bandwidth": parseInt(value),
          "nameGroup": calculatorMode[mode],
          "region": region,
          "tarif": parseInt(Math.round(priceX))
        };
      case 1:
      case 4:
        priceX = minVal[`${region.toLowerCase()}${isGlobalOrSource ? 'Global' : 'Domestik'}`] + (((value - minVal.bandwidthmbps) / (maxVal.bandwidthmbps - minVal.bandwidthmbps)) * (maxVal[`${region.toLowerCase()}${isGlobalOrSource ? 'Global' : 'Domestik'}`] - minVal[`${region.toLowerCase()}${isGlobalOrSource ? 'Global' : 'Domestik'}`]));
        return {
          "mode": mode,
          "bandwidth": parseInt(value),
          "isGlobal": isGlobalOrSource,
          "nameGroup": calculatorMode[mode],
          "region": region,
          "tarif": parseInt(Math.round(priceX))
        };
      case 2:
        const priceXBestEffort = Object.values(_result[0][0])[0] + (((value - Object.values(_result[0][0])[1]) / (Object.values(_result[1][0])[1] - Object.values(_result[0][0])[1])) * (Object.values(_result[1][0])[0] - Object.values(_result[0][0])[0]));
        const priceXInteraktif = Object.values(_result[0][1])[0] + (((value - Object.values(_result[0][1])[1]) / (Object.values(_result[1][1])[1] - Object.values(_result[0][1])[1])) * (Object.values(_result[1][1])[0] - Object.values(_result[0][1])[0]));      // const priceXInteraktif = _minVal[1].bandwidthmbps + (((value - _minVal[1].bandwidthmbps) / (_maxVal[1].bandwidthmbps - _minVal[1].bandwidthmbps)) * (_maxVal[1][`${region}${index === 1 ? 'Global' : 'Domestik'}`] - _minVal[1][`${region}${index === 0 ? 'Global' : 'Domestik'}`]));
        return {
          "mode": mode,
          "submode": subModeMetro,
          "bandwidth": parseInt(value),
          "isSource": isGlobalOrSource,
          "nameGroup": calculatorMode[mode],
          "region": region,
          "tarif": [
            { [Object.keys(_result[0][0])[0]]: Math.round(priceXBestEffort) },
            { [Object.keys(_result[0][1])[0]]: Math.round(priceXInteraktif) }
          ]
        };
      default:
        break;
    }
  } else {
    if (mode === 0 || mode === 1) {
      preResult = data.rows.filter(item =>
        item.bandwidthmbps === parseInt(value)
      );
    } else if (mode === 2) {
      preResult = data.filter(item =>
        Object.keys(item)[0] === region
      );
    } else {
      preResult = data.rows.filter(item =>
        item.bandwidthmbps === parseInt(value)
      );
    }

    switch (mode) {
      case 0:
      case 3:
      case 5:
        result = preResult[0][region.toLowerCase()];
        return {
          "mode": mode,
          "bandwidth": parseInt(value),
          "nameGroup": calculatorMode[mode],
          "region": region,
          "tarif": parseInt(result)
        };
      case 1:
      case 4:
        result = preResult[0][region.toLowerCase() + (isGlobalOrSource ? 'Global' : 'Domestik')];
        return {
          "mode": mode,
          "bandwidth": parseInt(value),
          "isGlobal": isGlobalOrSource,
          "nameGroup": calculatorMode[mode],
          "region": region,
          "tarif": parseInt(result)
        };
      case 2:
        result = preResult[0][region].filter(item => item.bandwidthmbps === parseInt(value));
        filteredKey = Object.keys(result[0]).filter(item => (item.toLowerCase().includes(isInter ? '_inter' : '_intra') && item.includes(isMP2MP ? 'MP2MP' : 'P2P')));
        result.filter(item =>
          filteredKey.map((key, index) => {
            _result[index] = { [key]: item[key] };
            return false;
          })
        );

        return {
          "mode": mode,
          "submode": subModeMetro,
          "bandwidth": parseInt(value),
          "isSource": isGlobalOrSource,
          "nameGroup": calculatorMode[mode],
          "region": region,
          "tarif": _result
        };
      default:
        break;
    }
  }
}

export default calculate;