const AstinetRegulerPriceList = async () => {
    try {
        const query = await fetch(AstinetRegulerEndpoint); // Load backend
        const response = await query.json();

        const styledResponse = response.rows.map((item, index) => {
            item.satelit = item.menggunakanbackbonesatelit;

            // Hapus nama keys yang belum
            delete item.menggunakanbackbonesatelit;
            return item;
        });
        return { rows: styledResponse };
    } catch (err) {
        console.log(err);
    }
}

const MetroPriceList = async () => {
    let metroRawPriceList = [];
    try {
        for (let i = 0; i < 10; i++) {
            const query = await fetch(`${MetroEndpoint}&sheet=${i + 3}&columns=false`); // Load backend
            const response = await query.json();
            metroRawPriceList[i] = response;
        }

        for (let j = 0; j < metroRawPriceList.length; j++) {
            metroRawPriceList[j][regionMetro[j]] = metroRawPriceList[j].rows;
            delete metroRawPriceList[j].rows;

            metroRawPriceList[j][regionMetro[j]].map((item, index) => {
                // Intra
                item._intraP2P_P2MPBestEffort = item.intra;
                item._intraP2P_P2MPInteraktif = item._cpzh4;
                item._intraMP2MPBestEffort = item._cre1l;
                item._intraMP2MPInteraktif = item._chk2m;

                // Inter
                item._interP2P_P2MPBestEffort = item.inter;
                item._interP2P_P2MPInteraktif = item._ckd7g;

                // NNI
                item._nniP2P_P2MPBestEffort = item.nni;
                item._nniP2P_P2MPInteraktif = item._cyevm;

                delete item.intra;
                delete item._cpzh4;
                delete item._cre1l;
                delete item._chk2m;
                delete item.inter;
                delete item._ckd7g;
                delete item.nni;
                delete item._cyevm;
                delete item._d180g; // kolom k
                delete item._d2mkx; //kolom l
                return item;
            });
        }

        return metroRawPriceList;
    } catch (err) {
        console.log(err);
    }
}



export { IPTransitBedaBWPriceList, IPTransitMixPriceList, MetroPriceList, AstinetLitePriceList, AstinetBedaBWPriceList, AstinetRegulerPriceList };